import React from "react";


const Example = () => {
    return (
        <>
            <div className="container">
                <h1>Сайт является примером работы API</h1>
                <p>Здесь присутствует выгрузка следующих категорий:</p>
                <ul>
                    <li>-   Склады</li>
                    <li>-   Бренды</li>
                    <li>-   Товары</li>
                    <li>-   Слайдер</li>
                </ul>
                <p>Новые данные добавляются в админ-панели.</p>
                <p>Показаны фильтрации брендов и добавления товаров в корзину</p>
            </div>
        </>
    );
};

export default Example;
