import React, { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";


const Catg = () => {
  const [brands, setBrands] = useState([]);
  React.useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get(
          "https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/public/api/brands/limit/6"
        );

        setBrands(data);
        console.log(data);
      } catch (error) {
        console.log(error);
      }
    };

    fetchData();
  }, []);

  return (
    <>
      <div className="category">
        <div className="chead d_flex">
          <h1>Бренды </h1>
          <h1>Магазин </h1>
        </div>
        {brands.map((value, index) => {
          return (
            <Link to={`/items/brand/${value.id}`} className="box f_flex" key={index}>
              <img
                src={`https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/${value.photo}`}
                style={{ height: "20px" }}
              />
              <span>{value.name}</span>
            </Link>
          );
        })}
        <div className="box box2">
          <Link to={`/brands`}>Посмотреть все бренды</Link>
        </div>
      </div>
    </>
  );
};

export default Catg;
