import React, { useState } from "react"
import Slider from "react-slick"
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"
// import Ddata from "./Ddata"
import "../newarrivals/style.css"
import axios from "axios"

const Dcard = () => {
  const [shopItems, setItems] = useState([]);
  React.useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get(
          "https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/public/api/items"
        );

        setItems(data);
        console.log(data);
      } catch (error) {
        console.log(error);
      }
    };

    fetchData();
  }, []);

  const settings = {
    dots: false,
    infinite: true,
    slidesToShow: 6,
    slidesToScroll: 1,
    autoplay: true,
  }
  return (
    <>
      <Slider {...settings}>
        {shopItems.map((value, index) => {
          return (
            <>
              <div className='box product' key={index}>
                <div className='img'>
                  <img src={`https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/${value.photo}`} alt='' width='100%' />
                </div>
                <h4>{value.name}</h4>
                <span>{value.price} р</span>
              </div>
            </>
          )
        })}
      </Slider>
    </>
  )
}

export default Dcard
