import React, { useState } from "react";
import axios from "axios";
import Slider from "react-slick"
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"
// import Tdata from "./Tdata"
import { Link } from "react-router-dom";

const buttonStyle = {
  background: '#0f3460',
  color: 'white',
  'border-radius': '10px',
  padding: '5px 10px'
}

const TopCart = () => {
  const [Tdata, setData] = useState([]);
  React.useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get(
          "https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/public/api/brands"
        );

        setData(data);
        console.log(data);
      } catch (error) {
        console.log(error);
      }
    };

    fetchData();
  }, []);

  const settings = {
    dots: false,
    infinite: true,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
  }
  return (
    <>
      <Slider {...settings}>
        {Tdata.map((value, index) => {
          return (
            <>
              <div className='box product' key={index}>
                <div className='nametop d_flex'>
                  <span className='tleft'>{value.name}</span>
                  <span className='tright'>{value.type_name}</span>
                </div>
                <div className='img'>
                  <img src={`https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/${value.photo}`} alt='' />
                </div>
                <Link to={`/items/brand/${value.id}`} style={buttonStyle}>Перейти</Link>
              </div>
            </>
          )
        })}
      </Slider>
    </>
  )
}

export default TopCart
