import React, { useState } from "react";
import axios from "axios";
// import Ndata from "./Ndata"

const Cart = () => {
  const [Ndata, setData] = useState([]);
  React.useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get(
          "https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/public/api/items/limit/6"
        );

        setData(data);
        console.log(data);
      } catch (error) {
        console.log(error);
      }
    };

    fetchData();
  }, []);

  return (
    <>
      <div className='content grid product'>
        {Ndata.map((val, index) => {
          return (
            <div className='box' key={index}>
              <div className='img'>
                <img src={`https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/${val.photo}`} alt='' />
              </div>
              <h4>{val.name}</h4>
              <span>${val.price}</span>
            </div>
          )
        })}
      </div>
    </>
  )
}

export default Cart
