import React, { useState } from "react"
// import Sdata from "./Sdata"
import axios from 'axios'
import Slider from "react-slick"
import "slick-carousel/slick/slick.css"
import "slick-carousel/slick/slick-theme.css"

const SlideCard = () => {
  const [sliderCard, setSliderCard] = useState([]);
  React.useEffect(() => {
    const fetchData = async () => {
      try {
        const { data } = await axios.get(
          "https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/public/api/slider"
        );

        setSliderCard(data);
        console.log(data);
      } catch (error) {
        console.log(error);
      }
    };

    fetchData();
  }, []);

  const settings = {
    dots: true,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    appendDots: (dots) => {
      return <ul style={{ margin: "0px" }}>{dots}</ul>
    },
  }
  return (
    <>
      <Slider {...settings}>
        {sliderCard.map((value, index) => {
          return (
            <>
              <div className='box d_flex top' key={index}>
                <div className='left'>
                  <h1>{value.title}</h1>
                  <p>{value.description}</p>
                  {value.url && <button className='btn-primary'>Подробнее</button>}
                </div>
                <div className='right'>
                  <img src={`https://home.xn-----6kcbivs1aohfys0g.xn--p1ai/${value.photo}`} style={{ height: "300px" }} />
                </div>
              </div>
            </>
          )
        })}
      </Slider>
    </>
  )
}

export default SlideCard
